import { TaskCardBlock } from "../ui/TaskCardBlock";
import type { Meta, StoryObj } from "@storybook/react";
import React, { useState } from "react";
import { Popover, PopoverTrigger, PopoverContent } from "../ui/popover";
import { Badge } from "../ui/badge";

const meta: Meta<typeof TaskCardBlock> = {
  title: "Blocks/TaskCardBlockB",
  component: TaskCardBlock,
};
export default meta;

type Story = StoryObj<typeof TaskCardBlock>;

const mockItem = {
  id: 1,
  title: "№1. Средства связи предложений в тексте",
};
const mockCategory = "Синтаксис";
const mockCategories = ["Синтаксис", "Орфография", "Пунктуация"];
const mockItemStats = { easiest: 2, easy: 5, medium: 10, hard: 6, hardest: 1 };
const mockDifficultyTiers = [
  { id: "easiest", label: "Самые лёгкие" },
  { id: "easy", label: "Лёгкие" },
  { id: "medium", label: "Средние" },
  { id: "hard", label: "Сложные" },
  { id: "hardest", label: "Самые сложные" },
];

export const FullCard_B: Story = {
  render: () => {
    const categories = [
      "Орфография",
      "Пунктуация",
      "Лексика",
      "Морфология",
    ];
    const [currentCount, setCurrentCount] = useState(0);
    const [difficulties, setDifficulties] = useState<string[]>(["any"]);
    const [catSelected, setCatSelected] = useState<string[]>([...categories]);
    const allTierIds = mockDifficultyTiers.map((t) => t.id);
    
    // Логика выбора сложности
    const handleDiffChange = (id: string) => {
      if (id === "any") {
        setDifficulties(["any"]);
        return;
      }
      let newValue = difficulties.includes(id)
        ? difficulties.filter((v) => v !== id)
        : [...difficulties.filter((v) => v !== "any"), id];
      if (newValue.length === 0) newValue = ["any"];
      if (newValue.length === allTierIds.length) newValue = ["any"];
      setDifficulties(newValue);
    };
    
    // Логика выбора категорий
    const handleCatChange = (cat: string) => {
      let newSelected;
      if (catSelected.includes(cat)) {
        newSelected = catSelected.filter((c) => c !== cat);
      } else {
        newSelected = [...catSelected, cat];
      }
      if (newSelected.length === 0) newSelected = [...categories];
      if (newSelected.length === categories.length) newSelected = [...categories];
      setCatSelected(newSelected);
    };
    
    // DropdownChips для сложности
    const [diffOpen, setDiffOpen] = useState(false);
    const getDiffLabel = () => {
      if (difficulties.length === 1 && difficulties[0] === "any") return "любая сложность";
      const labels = difficulties
        .map(
          (v) =>
            v === "easiest"
              ? "самые лёгкие"
              : v === "easy"
              ? "лёгкие"
              : v === "medium"
              ? "средние"
              : v === "hard"
              ? "сложные"
              : v === "hardest"
              ? "самые сложные"
              : v.toLowerCase()
        );
      if (labels.length === 1) return labels[0];
      if (labels.length === 2) return `${labels[0]} и ${labels[1]}`;
      if (labels.length > 2) return `${labels[0]}, ${labels[1]} +${labels.length - 2}`;
      return "";
    };
    
    // DropdownChips для категорий
    const [catOpen, setCatOpen] = useState(false);
    const getCatLabel = () => {
      if (catSelected.length === categories.length) return "все категории";
      const labels = catSelected.map((c) => c.toLowerCase());
      if (labels.length === 1) return labels[0];
      if (labels.length === 2) return `${labels[0]} и ${labels[1]}`;
      if (labels.length > 2) return `${labels[0]}, ${labels[1]} +${labels.length - 2}`;
      return "";
    };
    
    // SVG шеврона
    const Chevron = ({ open }: { open: boolean }) => (
      <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg" style={{ marginLeft: 6, transition: "transform 0.2s", transform: open ? "rotate(180deg)" : "rotate(0deg)" }}>
        <path d="M4.5 6L8 9.5L11.5 6" stroke="#888" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" />
      </svg>
    );
    
    // Кнопка закрытия для мобильных
    const MobileCloseButton = ({ setOpen }: { setOpen: (v: boolean) => void }) => (
      <button onClick={() => setOpen(false)} style={{ display: "block", width: "100%", marginTop: 10, padding: "8px 0", background: "#f3f3f3", border: "none", borderRadius: 6, color: "#333", fontSize: 14, fontWeight: 500, cursor: "pointer" }} className="mobile-only">Закрыть</button>
    );
    
    // Dropdown для сложности с счетчиками
    const difficultyDropdown = (
      <Popover open={diffOpen} onOpenChange={setDiffOpen}>
        <PopoverTrigger asChild>
          <Badge variant="outline" className="cursor-pointer py-1 px-3 flex items-center gap-1.5 hover:bg-gray-100 transition-colors dropdown-badge" style={{ minWidth: 0, width: "fit-content", maxWidth: "none", paddingRight: 10, paddingLeft: 14, display: "flex", alignItems: "center", justifyContent: "space-between", whiteSpace: "nowrap", fontWeight: 400 }}>
            <span className="text-xs" style={{ overflow: "hidden", textOverflow: "ellipsis", fontWeight: 400 }}>{getDiffLabel()}</span>
            <Chevron open={diffOpen} />
          </Badge>
        </PopoverTrigger>
        <PopoverContent className="w-auto p-2" style={{ minWidth: 220, maxWidth: 320, display: "flex", flexWrap: "wrap", gap: 8, alignItems: "stretch" }}>
          <div style={{ display: "flex", flexWrap: "wrap", gap: 8 }}>
            <Badge key="any" variant={difficulties.length === 1 && difficulties[0] === "any" ? "default" : "outline"} className={`cursor-pointer transition-colors text-xs px-2 py-0.5 flex items-center gap-1 ${difficulties.length === 1 && difficulties[0] === "any" ? "bg-teal-600 hover:bg-teal-700 text-white border-teal-600" : "border-gray-300 text-gray-700 hover:bg-gray-100"}`} style={{ fontWeight: 400 }} onClick={() => handleDiffChange("any")}>любая сложность <span className="text-[14px] font-semibold leading-5">{Object.values(mockItemStats).reduce((sum, count) => sum + count, 0)}</span></Badge>
            {mockDifficultyTiers.map((tier) => (
              <Badge key={tier.id} variant={difficulties.includes(tier.id) && difficulties[0] !== "any" ? "default" : "outline"} className={`cursor-pointer transition-colors text-xs px-2 py-0.5 flex items-center gap-1 ${difficulties.includes(tier.id) && difficulties[0] !== "any" ? "bg-teal-600 hover:bg-teal-700 text-white border-teal-600" : "border-gray-300 text-gray-700 hover:bg-gray-100"}`} style={{ fontWeight: 400 }} onClick={() => handleDiffChange(tier.id)}>{tier.label.toLowerCase()} <span className="text-[14px] font-semibold leading-5">{(mockItemStats as any)[tier.id]}</span></Badge>
            ))}
          </div>
          <div className="desktop-close-hint text-xs" style={{ marginTop: 10, color: "#888", textAlign: "center" }}>ESC или клик вне меню для закрытия</div>
          <div className="mobile-close-btn" style={{ display: "none" }}><MobileCloseButton setOpen={setDiffOpen} /></div>
          <style>{`
            @media (max-width: 600px) {
              .mobile-close-btn { display: block !important; }
              .desktop-close-hint { display: none !important; }
              .dropdown-badge { width: calc(100vw - 32px) !important; margin: 0 16px !important; max-width: 100vw !important; }
            }
            .dropdown-badge, .dropdown-badge * { font-weight: 400 !important; }
          `}</style>
        </PopoverContent>
      </Popover>
    );
    
    // Dropdown для категорий
    const categoryDropdown = (
      <Popover open={catOpen} onOpenChange={setCatOpen}>
        <PopoverTrigger asChild>
          <Badge variant="outline" className="cursor-pointer py-1 px-3 flex items-center gap-1.5 hover:bg-gray-100 transition-colors dropdown-badge" style={{ minWidth: 0, width: "fit-content", maxWidth: "none", paddingRight: 10, paddingLeft: 14, display: "flex", alignItems: "center", justifyContent: "space-between", whiteSpace: "nowrap", fontWeight: 400 }}>
            <span className="text-xs" style={{ overflow: "hidden", textOverflow: "ellipsis", fontWeight: 400 }}>{getCatLabel()}</span>
            <Chevron open={catOpen} />
          </Badge>
        </PopoverTrigger>
        <PopoverContent className="w-auto p-2" style={{ minWidth: 220, maxWidth: 320, display: "flex", flexWrap: "wrap", gap: 8, alignItems: "stretch" }}>
          <div style={{ display: "flex", flexWrap: "wrap", gap: 8 }}>
            <Badge key="all" variant={catSelected.length === categories.length ? "default" : "outline"} className={`cursor-pointer transition-colors text-xs px-2 py-0.5 flex items-center gap-1 ${catSelected.length === categories.length ? "bg-teal-600 hover:bg-teal-700 text-white border-teal-600" : "border-gray-300 text-gray-700 hover:bg-gray-100"}`} style={{ fontWeight: 400 }} onClick={() => setCatSelected([...categories])}>Все категории</Badge>
            {categories.map((cat) => (
              <Badge key={cat} variant={catSelected.includes(cat) && catSelected.length !== categories.length ? "default" : "outline"} className={`cursor-pointer transition-colors text-xs px-2 py-0.5 flex items-center gap-1 ${catSelected.includes(cat) && catSelected.length !== categories.length ? "bg-teal-600 hover:bg-teal-700 text-white border-teal-600" : "border-gray-300 text-gray-700 hover:bg-gray-100"}`} style={{ fontWeight: 400 }} onClick={() => { if (catSelected.length === categories.length) { setCatSelected([cat]); } else { handleCatChange(cat); } }}>{cat}</Badge>
            ))}
          </div>
          <div className="desktop-close-hint text-xs" style={{ marginTop: 10, color: "#888", textAlign: "center" }}>ESC или клик вне меню для закрытия</div>
          <div className="mobile-close-btn" style={{ display: "none" }}><MobileCloseButton setOpen={setCatOpen} /></div>
          <style>{`
            @media (max-width: 600px) {
              .mobile-close-btn { display: block !important; }
              .desktop-close-hint { display: none !important; }
              .dropdown-badge { width: calc(100vw - 32px) !important; margin: 0 16px !important; max-width: 100vw !important; }
            }
            .dropdown-badge, .dropdown-badge * { font-weight: 400 !important; }
          `}</style>
        </PopoverContent>
      </Popover>
    );
    
    return (
      <div style={{ width: "100%" }}>
        <TaskCardBlock
          item={{ ...mockItem, title: "№1. Средства связи предложений в тексте" }}
          category={null}
          currentCount={currentCount}
          maxCount={20}
          onDecrement={() => setCurrentCount((c) => Math.max(0, c - 1))}
          onIncrement={() => setCurrentCount((c) => Math.min(20, c + 1))}
          difficulties={difficulties}
          onDifficultyChange={handleDiffChange}
          categories={catSelected}
          onCategoriesChange={setCatSelected}
          itemStats={mockItemStats}
          difficultyTiers={mockDifficultyTiers}
          difficultyDropdown={difficultyDropdown}
          categoryDropdown={categoryDropdown}
        />
        <style>{`
          @media (max-width: 600px) {
            .task-card-block-controls {
              flex-direction: column !important;
              align-items: flex-start !important;
              gap: 10px !important;
            }
            .dropdown-badge, .category-dropdown-badge, .difficulty-dropdown-badge {
              width: auto !important;
              max-width: 95vw !important;
              margin: 0 !important;
            }
            .popover-close-btn {
              width: 100% !important;
              padding: 12px 0 !important;
              margin-top: 12px !important;
              border-radius: 8px !important;
              background: #f3f3f3 !important;
              font-size: 16px !important;
              font-weight: 500 !important;
              border: none !important;
              text-align: center !important;
              cursor: pointer !important;
              color: #222 !important;
              box-shadow: none !important;
              display: block !important;
            }
          }
        `}</style>
      </div>
    );
  },
}; 